import { Injectable } from '@angular/core';
import firebase from 'firebase';
import { User, AuthCredential } from '@firebase/auth-types';
import { Reference } from '@firebase/database-types';

/*
  Generated class for the ProfileProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  an  updateInterests: any;
d Angular DI.
*/

@Injectable()

export class ProfileProvider {
  userProfile: Reference;
  currentUser: User;

  constructor() {
    firebase.auth().onAuthStateChanged(user => {
      if (user) {
        this.currentUser = user;
        this.userProfile = firebase.database().ref(`/userProfile/${user.uid}`);
      }
    });
  }


  getUserProfile(): Reference {
    return this.userProfile;
  }

  updateName(firstName: string, lastName: string): Promise<any> {
    return this.userProfile.update({ firstName, lastName });
  }

  updateDOB(DOB: string): Promise<any> {
    return this.userProfile.update({ DOB });
  }

  updateEmail(newEmail: string, password: string): Promise<any> {
    const credential: AuthCredential = firebase.auth.EmailAuthProvider.credential(
      this.currentUser.email,
      password
    );
    return this.currentUser.reauthenticateWithCredential(credential).then(user => {
      this.currentUser.updateEmail(newEmail).then(user => {
        this.userProfile.update({ email: newEmail });
      });
    }).catch(error => {
      console.error(error);
    });
  }

  updatePassword(newPassword: string, oldPassword: string): Promise<any> {
    const credential: AuthCredential = firebase.auth.EmailAuthProvider.credential(
      this.currentUser.email,
      oldPassword
    );
    return this.currentUser.reauthenticateWithCredential(credential).then(user => {
      this.currentUser.updatePassword(newPassword).then(user => {
        console.log('Password Changed');
      });
    }).catch(error => {
      console.error(error);
    });
  }

  updateInterests(interestList: any[]): Promise<any> {
    return this.userProfile.update({ interestList });
  }

  updateGender(gender: string): Promise<any> {
    return this.userProfile.update({ gender });
  }

  updateCountry(country: string): Promise<any> {
    return this.userProfile.update({ country });
  }

}


